/* Block */
.breadcrumbs-section {
  padding-top: 70px;
  padding-bottom: 50px;
}
.breadcrumbs-section .breadcrumb {
  padding: 0;
  margin: 0;
  background-color: transparent;
}
.breadcrumbs-section .breadcrumb .breadcrumb-item {
  position: relative;
  font-size: 14px;
  font-weight: 500;
}
.breadcrumbs-section .breadcrumb .breadcrumb-item.active {
  color: #2B478B;
}
.breadcrumbs-section .breadcrumb a {
  color: #767676;
}
.breadcrumbs-section .breadcrumb .breadcrumb-item + .breadcrumb-item {
  padding-left: 30px;
}
.breadcrumbs-section .breadcrumb .breadcrumb-item + .breadcrumb-item::before {
  content: "";
  position: absolute;
  top: calc(50% - 4px);
  left: calc(15px - 4px);
  width: 5px;
  height: 8px;
  background-image: url(../../assets/img/svg/angle-right.svg);
  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
}

@media (max-width: 991px) {
  .breadcrumbs-section {
    padding-top: 20px;
  }
}
.article-card img {
  margin-bottom: 30px;
}
.article-card .article-title {
  font-size: 24px;
  font-weight: 700;
}

@media (max-width: 991px) {
  .article-card img {
    width: 100%;
    margin-bottom: 20px;
  }
}
@media (max-width: 575px) {
  .article-card .article-title {
    font-size: 20px;
  }
}
.load-more-wrap {
  padding: 100px 0 60px;
  display: flex;
  justify-content: center;
}

.pagination-nav {
  display: flex;
  justify-content: center;
}
.pagination-nav .page-item[aria-current=page] .page-link {
  position: relative;
}
.pagination-nav .page-item[aria-current=page] .page-link::before {
  content: "";
  position: absolute;
  bottom: 3px;
  width: 10px;
  height: 1px;
  background-color: #767676;
}
.pagination-nav .page-item:first-child {
  margin-right: 10px;
}
.pagination-nav .page-item:first-child .page-link {
  background-color: #F5F5F5;
}
.pagination-nav .page-item:first-child .page-link:hover {
  background-color: #F5F5F5 !important;
}
.pagination-nav .page-item:last-child {
  margin-left: 10px;
}
.pagination-nav .page-item:last-child .page-link {
  background-color: #F5F5F5;
}
.pagination-nav .page-item:last-child .page-link:hover {
  background-color: #F5F5F5 !important;
}
.pagination-nav .page-link {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0;
  width: 30px;
  height: 30px;
  color: #767676;
  border: none;
  border-radius: 50% !important;
  transition: all 0.2s ease-in-out;
  font-weight: 500;
  background-color: transparent;
}
.pagination-nav .page-link:focus {
  box-shadow: none;
}
.pagination-nav .page-link svg {
  transition: all 0.2s ease-in-out;
  fill: #767676;
}
.pagination-nav .page-item:not([aria-current=page]) .page-link:hover {
  color: #232323;
  background-color: #FFCC00;
}
.pagination-nav .page-item:not([aria-current=page]) .page-link:hover svg {
  fill: #232323;
}

@media (max-width: 575px) {
  .load-more-wrap {
    padding: 60px 0 40px;
  }

  .pagination-nav .pagination {
    flex-wrap: wrap;
    align-items: center;
    position: relative;
    padding: 0 40px;
  }
  .pagination-nav .page-item:last-child, .pagination-nav .page-item:first-child {
    position: absolute;
  }
  .pagination-nav .page-item:first-child {
    left: 0;
  }
  .pagination-nav .page-item:last-child {
    right: 0;
  }
}
/* Section */
.useful-links-section .row {
  margin: -10px;
}
.useful-links-section .col-link {
  padding: 10px;
}
.useful-links-section .col-link:nth-child(1) .link-wrap {
  background-color: #FFCC00;
}
.useful-links-section .col-link:nth-child(2) .link-wrap {
  background-color: #FED638;
}
.useful-links-section .col-link:nth-child(3) .link-wrap {
  background-color: #FFDC52;
}
.useful-links-section .col-link:nth-child(4) .link-wrap {
  background-color: #FFE98F;
}
.useful-links-section .col-link a:hover {
  text-decoration: none;
}
.useful-links-section .col-link a:hover .learn-more .svg-icon {
  transform: rotate(-180deg);
}
.useful-links-section .col-link .link-wrap {
  height: 100%;
  display: flex;
  flex-direction: column;
  padding: 40px 20px;
  border-radius: 15px;
}
.useful-links-section .col-link .link-wrap .h2 {
  margin-bottom: 20px;
}
.useful-links-section .col-link .link-wrap p {
  margin-bottom: 20px;
  font-size: 15px;
  font-weight: 500;
  flex: 1;
}
.useful-links-section .col-link .link-wrap .svg-icon {
  margin-right: 5px;
  transition: all 0.2s ease-in-out;
}
.useful-links-section .col-link .link-wrap .learn-more {
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 24px;
  font-weight: 600;
}

.articles-section .row {
  margin: 0 -10px;
}
.articles-section .article-col {
  margin-bottom: 30px;
  padding: 0 10px;
}
.articles-section .article-col:last-child {
  margin-bottom: 0;
}
.articles-section .article-card .article-title {
  font-weight: 600;
}
.articles-section .article-card img {
  border-radius: 8px;
}